$(document).ready(function () {
    /* Mostrar el modal con la advertencia */
    $('#aviso_administrador').modal('show');

    /* Mandar a llamar la tabla de las ordenes */
    Tabla("datos_ordenes", "obtener_ordenes.php");

    /* Mandar a llamar la tabla de auditoria */
    Tabla("datos_auditar", "obtener_auditar.php");

    $("#Crear").click(function () {
        $("#formulario_producto")[0].reset();
        $("#action").val("Crear");
        $("#Operacion").val("Crear");
        $("#Foto_Subida").html("");
    });

    /* Mandar a llamar la tabla de auditoria */
    Tabla("datos_productos", "obtener_productos.php");

    /* Mandar a llamar las cards del bloque principal */
    cartas("productosPrimario.php", "card-container");

    /* Mandar a llamar las cards del bloque secundario */
    cartas("productosSecundario.php", "card-container-2");

    /* Mandar a llamar la tabla del carrito */
    Tabla("datos_carrito", "obtener_carrito.php");

    /* Mandar a llamar la tabla del pedidos pendientes */
    Tabla("datos_pedidos", "obtener_pedidos.php");
});

/* Codigo para validar el login de los administradores */
$(document).on('submit', '#login_admin', function (event) {
    event.preventDefault();
    const email = $("#email").val();
    const pass = $("#contraseña").val();
    $.ajax({
        url: "/includes/functions/login_validate.php",
        type: "POST",
        data: { correo: email, contraseña: pass, rol: "administradores" },
        success: function (data) {
            data = JSON.parse(data);
            if (data.status == "success") {
                alert(data.message);
                window.location.href = '/admin/propiedades/ordenes.php';
            }
            else {
                alert(data.message);
                $("#contraseña").val("");
            }
        },
        error: function (textStatus, errorThrown) {
            alert(textStatus + errorThrown);
        }
    });
});

/* Script para el dataTable */
function Tabla(id, url) {
    let dataTable = $('#' + id).DataTable({
        "processing": true,
        "serverSide": true,
        "order": [],
        "ajax": {
            url: "/includes/functions/" + url,
            type: "POST",
        },
        "columnsDefs": [{
            "targets": [0, 3, 4],
            "orderable": false,
        },
        ],
        "language": {
            "decimal": "",
            "emptyTable": "No hay registros",
            "info": "Mostrando _START_ a _END_ de _TOTAL_ Entradas",
            "infoEmpty": "Mostrando 0 to 0 of 0 Entradas",
            "infoFiltered": "(Filtrado de _MAX_ total entradas)",
            "infoPostFix": "",
            "thousands": ",",
            "lengthMenu": "Mostrar _MENU_ Entradas",
            "loadingRecords": "Cargando...",
            "processing": "Procesando...",
            "search": "Buscar:",
            "zeroRecords": "Sin resultados encontrados",
            "paginate": {
                "first": "Primero",
                "last": "Ultimo",
                "next": "Siguiente",
                "previous": "Anterior"
            }
        }
    });
}

/* Codigo para crear/actualizar - Productos */
$(document).on('submit', '#formulario_producto', function (event) {
    event.preventDefault();
    let imagen = $(".img-thumnail").val();
    // alert(imagen);
    let extension = $("#foto").val().split('.').pop().toLowerCase();
    /* Busca un formato de imagen valido */
    if (extension != '') {
        if (jQuery.inArray(extension, ['gif', 'png', 'jpg', 'jpeg']) == -1) {
            $('#foto').val('');
            alert("Formato de imagen invalido");
            return false;
        }
    }
    $.ajax({
        url: "/includes/functions/producto.php",
        type: "POST",
        data: new FormData(this),
        contentType: false,
        processData: false,
        success: function (data) {
            data = JSON.parse(data);
            if (data.status == "success") {
                alert(data.message);
                $('#formulario_producto')[0].reset();
                $('#ingresar_producto').modal('hide');
                // dataTable.ajax.reload();
                location.reload();
            }
            else {
                alert(data.message);
            }
        },
        error: function (textStatus, errorThrown) {
            alert(textStatus + errorThrown);
        }
    });
    // if (imagen != '') {
    // }
    // else {
    //     alert("La imagen es obligatoria")
    // }
});

/* Boton para editar desde el modal - Productos */
$(document).on('click', '.editarProducto', function () {
    let id = $(this).attr("id");
    $.ajax({
        url: "/includes/functions/obtener_un_producto.php",
        method: "POST",
        data: { id_producto: id },
        dataType: "json",
        success: function (data) {
            $('#ingresar_producto').modal('show');
            $('#nombre_producto').val(data.nombre_producto);
            $('#categoria').val(data.categoria);
            $('#descripcion').val(data.descripcion);
            $('#precio_unitario').val(data.precio_unitario);
            $('#inventario').val(data.inventario);
            $('#promocion').val(data.promocion);
            $('#Foto_Subida').html(data.foto);
            $('.modal-title').text("Editar Producto");
            $('#id_producto').val(id);
            $('#action').val("Actualizar");
            $('#Operacion').val("Editar");
        },
        error: function (jqXHR, textStatus, errorThrown) {
            alert(textStatus + errorThrown);
        }
    });
});

/* Codigo para eliminar - Productos */
$(document).on('click', '.borrarProducto', function () {
    let id = $(this).attr("id");
    if (confirm("Esta seguro de borrar este producto: " + id)) {
        $.ajax({
            url: "/includes/functions/producto.php",
            method: "POST",
            data: { id_producto: id, Operacion: "Borrar" },
            success: function (data) {
                data = JSON.parse(data);
                if (data.status == "success") {
                    alert(data.message);
                    // dataTable.ajax.reload();
                    location.reload();
                }
                else {
                    alert(data.message);
                }
            },
            error: function (jqXHR, textStatus, errorThrown) {
                alert(textStatus + errorThrown);
            }
        });
    }
    else {
        return false;
    }
});

