/* Codigo para validar que el usuario este registrado antes de comprar */
function validarComprar(id) {
    // alert(id);
    $.ajax({
        url: "/includes/functions/validarUsuario.php",
        method: "POST",
        success: function (data) {
            data = JSON.parse(data);
            if (data.status == "error") {
                alert(data.message);
                window.location.href = "/login.php"
            }
            else {
                $('#detalles' + id).modal('show');
            }
        },
        error: function (jqXHR, textStatus, errorThrown) {
            alert(textStatus + errorThrown);
        }
    });
}

/* Agrega articulos al carrito */
function carrito(id, opcion) {
    let cantidad = document.querySelector("#Cantidad" + id).value;
    $.ajax({
        url: "/includes/functions/carrito.php",
        method: "POST",
        data: { id: id, cantidad: cantidad },
        success: function (data) {
            data = JSON.parse(data);
            if (data.status == "success") {
                alert(data.message);
                if (data.promotion != "Ninguna") {
                    alert(`Tiene una promocion del ${data.promotion}`);
                }
                if (opcion == 1) {
                    $('#detalles' + id).modal('hide');
                }
                else {
                    window.location.href = "/carrito.php";
                }
            }
            else {
                alert(data.message);
            }

        }, error: function (jqXHR, textStatus, errorThrown) {
            alert(textStatus + errorThrown);
        }
    });
}

