/* Crear un usuario */
$(document).on('submit', '#crear_usuario', function (event) {
    event.preventDefault();
    $.ajax({
        url: "/includes/functions/crear_user.php",
        type: "POST",
        data: new FormData(this),
        contentType: false,
        processData: false,
        success: function (data) {
            data = JSON.parse(data);
            if (data.status == "success") {
                alert(`Tu cuenta se ha creado exitosamente, por favor inicia sesión llenando tu nombre de usuario y tu contraseña.`);
                $('#ingresar_usuario').modal('hide');
                $("#nombre_usuario").val("");
                $("#apellido_usuario").val("");
                $("#correo_user").val("");
                $("#direccion").val("");
                $("#contraseña_user").val("");
            }
            else if (data.status == "warning") {
                alert(data.message);
                $("#correo_user").val("");
                $("#contraseña_user").val("");
            }
            else {
                alert(data.message);
                $('#ingresar_usuario').modal('hide');
            }
        },
        error: function (textStatus, errorThrown) {
            alert(textStatus + errorThrown);
        }
    });
});

/* Codigo para actualizar la contraeña de lo usuarios desde ajustes */
$(document).on('submit', '#actulizarContra', function (event) {
    event.preventDefault();
    const contra1 = $("#contraseña1").val();
    const contra2 = $("#contraseña2").val();
    const id = $("#id").val();
    if (contra1 === contra2) {
        $.ajax({
            url: "/includes/functions/actualizarContra.php",
            type: "POST",
            data: { contra: contra1, id: id },
            success: function (data) {
                data = JSON.parse(data);
                if (data.status == "success") {
                    alert(data.message);
                    window.location.href = '/';
                }
                else {
                    alert(data.message);
                    $('#cambiarContraseña').modal('hide');
                }
            },
            error: function (textStatus, errorThrown) {
                alert(textStatus + errorThrown);
            }
        });
    }
    else {
        alert("La contraseña NO coincide");
        $("#contraseña2").val("");
    }
});

